.catalog {
  grid-template-columns: repeat(1, 200px);
  grid-auto-flow: row;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 198px;
  height: 406px;
  padding: 0;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  z-index: 1;
  transform: scale(1.2);
}

.card__image {
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
  display: flex;
}

.card__device-name {
  color: #060b35;
  text-align: left;
  width: 166px;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__device-name {
  color: #34568b;
}

.card__device-code {
  text-align: left;
  color: #616070;
  box-sizing: border-box;
  width: calc(100% - 32px);
  margin: 0 16px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__reviews {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  height: 16px;
  margin: 0 16px 24px;
  display: flex;
}

.card__text {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-price_2199 {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  text-transform: uppercase;
  color: #060b35;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
  outline: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__link {
  text-align: center;
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__item:not(:first-child) {
  padding-left: 20px;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link:active {
  color: #00acdc;
  box-shadow: 0 4px #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.logo {
  max-height: 40px;
}

* {
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}
